import Vue from 'vue';
import Router from 'vue-router';
import Home from "./components/Home";
import About from "./components/About";
import Projects from "./components/Projects"
Vue.use(Router)

export default new Router({
    routes: [
      {
        path: '/',
        name: 'home',
        component: Home
      },
      {
        path: '*',
        name: 'wildcard',
        component: Home
      },
      {
        path: '/projects',
        name: 'projects',
        component: Projects
      },
      {
        path: '/about',
        name: 'about',
        component: About
      }
    ]
})

